<!--  -->
<template>
  <div class="swipperMain">
      <swipper>
      <swipper-item v-for="item,index in banner" :key="index">
        <a href="#"><img :src="item.image"  @load="loadingComplete()"></a>
      </swipper-item>
    </swipper>
  </div>
</template>

<script>
import Swipper from 'components/common/Swipper/Swipper.vue'
import SwipperItem from 'components/common/Swipper/SwipperItem.vue'
export default {
 name:'',
 props:["banner"],
  data () {
    return {
      isLoad:false
    };
  },
  components:{
    SwipperItem,
    Swipper
  },
  methods: {
    loadingComplete(){
      if(!this.isLoad){
          this.$emit("loadingComplete")
          this.isLoad = true
      }
     
    }
  },  
}
</script>
