<!--  -->
<template>
  <div class="tabcontrol">
      <div v-for="(item,index) in titles" :key="index"  @click="changeIndex(index)" >
          <span :class="{ active_choose:currentIndex===index}">{{item}}</span>
     </div>
  </div>
</template>

<script>
export default {
 name:'',
  data () {
    return {
        currentIndex:0
    };
  },
  props:{
     titles:{
        type:Array,
        default(){
            return []
        }
     }
  },
  methods:{
      changeIndex(index){
          this.currentIndex = index
          this.$emit("submitIndex",index)   //子组件发射事件给父组件
      }
  }
}
</script>
<style scoped>
.tabcontrol{
    display: flex;
    width: 100%;
    padding-top:10px;
    padding-bottom:5px;
    text-align: center;
    background: white;
}
.tabcontrol>div{
    flex:1;
    font-size: 17px;
}
.active_choose{
    display: inline-block;
    width:45%;
    color:#f259a6;
    border-bottom: 3px solid #f259a6;
}
</style>