<!--  -->
<template>
  <div class="mainDetail" id="home">
    <nav-bar class="navbar">
      <template v-slot:middle> 购物街 </template>
    </nav-bar>
     <tab-control
        :titles="['流行', '新款', '精选']"
        @submitIndex="submitIndex($event)"
        ref="tabcontrol1"
        class="position"
        v-show="isTurn"
      ></tab-control>
    <!-- 必须得在这里给scroll设置一个高度 -->
    <scroll
      ref="scroll"
      class="content"
      @scroll="GetPosition($event)"
      :pullUpLoad="true"
      @pullingUp="pullingData()"
    >
      <main-swipper
        :banner="banner"
        @loadingComplete="loadingComplete()"
      ></main-swipper>
      <recommend-view :recommend="recommend"></recommend-view>
      <feature-view></feature-view>
      <tab-control
        :titles="['流行', '新款', '精选']"
        @submitIndex="submitIndex($event)"
        ref="tabcontrol"
        class="{fixed:isfixed}"
      ></tab-control>
      <goods-list :lists="goods[currentType].list"></goods-list>
    </scroll>
    <back-top
      class="moveto"
      @click.native="backTotop()"
      v-show="isshow"
    ></back-top>
  </div>
</template>

<script>
import NavBar from "components/common/NavBar/NavBar.vue";
import Scroll from "components/common/Scroll/Scroll.vue";

import MainSwipper from "./childMain/MainSwipper.vue";
import RecommendView from "./childMain/RecommendView.vue";
import FeatureView from "./childMain/FeatureView.vue";

import TabControl from "components/content/TabControl/TabControl.vue";
import GoodsList from "components/content/Goods/GoodsList.vue";

import { getHomeData, getHomeGoods } from "network/main.js";

import {itemListenerMixin,backTopMixin } from "common/mixin.js"
export default {
  name: "",
  data() {
    return {
      banner: [],
      recommend: [],
      goods: {
        pop: { page: 0, list: [] },
        new: { page: 0, list: [] },
        sell: { page: 0, list: [] },
      },
      currentType: "pop",        
      isshow: false,
      tarOffSetTop: 0,          //tarcontrol组件的高度
      isTurn:false,
      keepHeight:0,            // 离开主页时保存的高度
      ItemEvent:null
    };
  },
  components: {
    NavBar,
    Scroll,
    MainSwipper,
    RecommendView,
    FeatureView,
    TabControl,
    GoodsList,
    // BackTop,
  },

  methods: {
    /* 以下代码是跟数据的操作相关的代码 */
    submitIndex(index) {
      switch (index) {
        case 0:
          this.currentType = "pop";
          break;
        case 1:
          this.currentType = "new";
          break;
        case 2:
          this.currentType = "sell";
          break;
      }
      this.$refs.tabcontrol1.currentIndex = index
      this.$refs.tabcontrol.currentIndex = index
    },
    // backTotop() {
    //   // 点击箭头使其滚动到开头
    //   //  console.log( this.$refs.scroll.scroll)
    //   this.$refs.scroll.scrollTo(0, 0, 1000); // 滚动到特定位置 0 0 代表位置 2000 是动画执行的时长 ms
    // },
    GetPosition(pos) {
      // 获取滚动的坐标并判断是否小于-1000
      this.isshow = pos.y <= -1000;
      this.isTurn = pos.y<=(-this.tarOffSetTop) 
    },
    pullingData() {
      this.getHomeGoods(this.currentType);
    },

    loadingComplete() {
      /* 
      $el可以拿到组件对象里面的dom对象，这样就可以获取它距离定位的父元素的高度 */
      this.tarOffSetTop = this.$refs.tabcontrol.$el.offsetTop;
      
    },
    /* 以下方法是跟网络请求有关的代码 */
    getHomeData() {
      getHomeData()
        .then((res) => {
          this.banner = res.data.banner.list;
          this.recommend = res.data.recommend.list;
        })
        .catch((erro) => {
          console.log(erro);
        });
    },

    getHomeGoods(
      type // 获取首页商品数据
    ) {
      this.goods[type].page = this.goods[type].page + 1;
      let page = this.goods[type].page;
      getHomeGoods(type, page)
        .then((res) => {
          this.goods[type].list.push(...res.data.list);
          //当数据加载完毕以后通知better-scroll
          this.$refs.scroll.finishPullUp();
        })
        .catch((erro) => {
          console.log(erro);
        });
    },
  },
  created() {
    this.getHomeData();
    this.getHomeGoods("pop");
    this.getHomeGoods("new"); // 这里所以这么写是为了不让 created变得臃肿不堪
    this.getHomeGoods("sell");
    
  },
  mounted() {
  
  },
  deactivated() {
    this.keepHeight = this.$refs.scroll.getY()
    // console.log(this.keepHeight)
    this.$bus.$off("finishloading",this.ItemEvent)  // 移除this.ItemEvent这个回调函数的事件t
  },
  activated() { 
    this.$refs.scroll.scrollTo(0,this.keepHeight,0)
    this.$refs.scroll.refresh()
  },
  mixins:[itemListenerMixin,backTopMixin ]
};
</script>
<style scoped>
.mainDetail {
  position: relative;
  height: 100vh;
}
.navbar {
  background: #f150a1;
}
.moveto {
  position: fixed;
  bottom: 100px;
  right: 10px;
}
.content {
  width: 100%;
  position: absolute;
  top: 48px;
  bottom: 47px;
  overflow: hidden;
}
.fixed{
  position: fixed;
  top:44px;
}
.position{
  position: relative;
  top:44px;
  z-index: 9;
}
</style>